
import React from 'react';

//importing typewriter-effect
import Typewriter from "typewriter-effect";

function TypeWriter() {
    return (
        <div className="fadein typewriter">
            <Typewriter

                onInit={(typewriter) => {
                    typewriter
                        .typeString("Dveloppeur Front End")
                        .pauseFor(1500)
                        .deleteAll()
                        .typeString("Dveloppeur Back End")
                        .pauseFor(1500)
                        .deleteAll()
                        .typeString("Dveloppeur Full Stack")
                        .pauseFor(1500)
                        .deleteAll()
                        .start();
                }}

                options={{
                    autoStart: true,
                    loop: true,
                }}
            />
        </div>
    );
}

export default TypeWriter;
